.sidebar{
    flex: 1;
    border-right: 0.5px solid;
    min-height: 100vh;

    .top{
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .logo{
        font-size: 20px;
        font-weight: bold;
        color: purple;
    }
    .center{
        padding-left: 10px;
        ul{
            list-style: none;
            margin: 0;
            padding: 0;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
            }
            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover{
                    background-color: #ece8ff;
                }
                .icon{
                    font-size: 18px;
                    color: #7451f8;
                }

                span{
                    font-size: 15px;
                    font-weight: 300;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }
    .bottom{
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption{
            width: 20px;
            height: 20px;
            border-radius: 5px;
            margin: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            &:nth-child(1){
                background-color: whitesmoke;
            }
            &:nth-child(2){
                background-color: black;
            }
            &:nth-child(1){
                background-color: darkblue;
            }
        }
    }

}