.home{
    display: flex  ;
    .homeContainer{
    
        flex: 6;
        .widgets,.charts{
            display: flex;
            padding: 20px;
            gap: 20px;
        }
    }
    .charts{
        padding: 5px 20px;
    }

    .listContainer{
        box-shadow: 2px 4px 18px 1px rgba(193,185,185,1);
    -webkit-box-shadow: 2px 4px 18px 1px rgba(193,185,185,1);
    -moz-box-shadow: 2px 4px 18px 1px rgba(193,185,185,1);
    padding: 20px;
    margin: 20px;
    .listTitle{
        font-weight: 500;
        color: gray;
        margin-bottom: 15px;
    }
    }
    
}
